<script>
  import popupNavBar from "@/components/popupNavBar";

  export default {
    data() {
      return {
        // 已提现收益
        takedIncome: '0',
        // 未提现收益
        untakedIncome: '0',
        // 团队人数
        teamMembers: '0',
        // 团队业绩
        teamPerformance: '0',
        // 总资产
        deposit: '0',
        // 季池奖金
        poolReward: '0',
        // usdt余额
        usdtBalance: '0',
        // trx余额
        trxBalance: '0',
      }
    },

    components: {popupNavBar},

    // 页面挂载后触发的函数
    async mounted() {
    },

    // 页面创建后触发的函数
    async created() {
      await this.init();
    },

    methods: {
      toInvite() {
        this.$router.push({path: "/invite"});
      },
      toInvest() {
        this.$router.push({path: "/invest"});
      },

      // 初始化
      async init() {
        // 同步锁, 等$isResolve执行完毕了才会执行这个
        await this.$onLaunched;
        // 获取数据
        this.getData1();
      },

      // 判断用户有没有授权usdt给主和合约
      async isApproveUsdt() {
        let res = await this.$contract.allowanceUsdtMast();
        res = this.$util.div10e6(res);
        if (res > 1) {
          this.isApprove1 = true;
        } else {
          this.isApprove1 = false;
        }
        console.log('查询授权usdt给主和合约', res);
      },

      async getData1() {
        let address = await this.$contract.defaultAddress();
        address = address.base58;
        console.log("address:", address);
        let res = await this.$contract.userData(address);
        let dynamicIncome = res.dynamicIncome._hex;
        let poolReward = res.poolReward._hex;//====>对应主网页 "奖池奖金"
        let deposit = res.deposit._hex;//====>对应主网页 "存入资金"
        let maxIncome = res.maxIncome._hex;
        let isConsul = res.isConsul;
        let takedIncome = res.income._hex;
        let staticIncome = await this.$contract.calculateStaticIncome(address);
        console.log("userData", res);

        this.takedIncome = this.$util.hexTo10To6number3(res.income._hex);
        this.untakedIncome = this.$util.hexTo10To6number3(dynamicIncome) + this.$util.hexTo10To6number3(staticIncome);
        this.deposit = this.$util.hexTo10To6number3(deposit);
        this.poolReward = this.$util.hexTo10To6number3(poolReward);

        let usdtBalance = await this.$contract.getUsdtBalance(address);
        this.usdtBalance = this.$util.hexTo10To6number3(usdtBalance);
        let trxBalance = await this.$contract.getTRXBalance(address);
        this.trxBalance = this.$util.div10e6(trxBalance);

        this.teamMembers = await this.$contract.teamsLength(address);
        let perfRes = await this.$contract.performances(address);
        this.teamPerformance = this.$util.hexTo10To6number3(perfRes.performance._hex);
        // console.log(this.takedIncome, this.untakedIncome, this.deposit, this.usdtBalance, this.trxBalance)
      },


    },

  };
</script>

