<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  export default {
    async created() {
      localStorage.setItem("lang", 0);
      await this.connectWallet();
    },

    methods: {
      // 连接钱包
      async connectWallet() {
        console.log('开始波场钱包');
        let res = await this.$contract.isTronLink();
        // 给出无法点击弹框, 逼着用户必须退出去准备波场环境, 否者无法继续使用
        if (!res.status) {
          console.log('没有发现钱包');
          return;
        }
        // 有钱包
        console.log('有钱包');
        // 创建合约对象
        await this.$contract.createdObject();
        // 同步锁, 执行完后才会执行其他页面的$onLaunched函数; 执行到这说明波场钱包对象和波场合约对象都已经获取到了;
        await this.$isResolve();
      },
    }

  }
</script>
