<template>
  <div class="withdrawal">
    <div class="bgc">
      <van-form @submit="onSubmit">
        <backNavBar :title="$t('withdrawal.withdrawDeposit')" :right="$t('withdrawal.WithdrawalRecord')" path="/index" />
        <div class="vanField sta">
          <p>{{$t("withdrawal.TheStaticGains")}}</p>
          <van-field v-model="sta" name="sta" readonly></van-field>
        </div>
        <div class="vanField dynamic">
          <p>{{$t("withdrawal.DynamicBenefits")}}</p>
          <van-field v-model="dynamic" name="dynamic" readonly> </van-field>
        </div>
        <div class="vanField money">
          <p>{{$t("withdrawal.JackpotBonuses")}}</p>
          <van-field v-model="money" name="money" readonly> </van-field>
        </div>
        <div class="vanField service">
          <p>{{$t("withdrawal.serviceCharge")}}</p>
          <van-field v-model="takeFeePool" name="takeFeePool" readonly>
            <template #button><span class="color">USDT</span></template>
          </van-field>
        </div>
        <div class="box">
          <div class="hint">
            {{$t("withdrawal.hint")}} </div>
        </div>
        <div class="account">
          <span>{{$t("withdrawal.TheAccountNumber")}}</span
          ><span class="num">{{stats}}<span class="usdt">USDT</span></span>
        </div>
        <van-button block class="bottom">{{$t("withdrawal.withdrawDeposit")}}</van-button>
      </van-form>
    </div>
  </div>
</template>

<script>
import backNavBar from "@/components/backNavBar";
export default {
  data() {
    return {
      // 静态收益
      sta: "",
      // 动态收益
      dynamic: "",
      // 奖池奖金
      money: "",
      // 手续费
      takeFeePool: "2",
      // 到账数量
      stats:""
    };
  },
  components: {
    backNavBar
  },

  async created() {
    // 同步锁, 等$isResolve执行完毕了才会执行这个
    await this.$onLaunched;
    await this.getData();
  },

  methods: {
    // 获取数据
    async getData() {
      let address = await this.$contract.defaultAddress();
      address = address.base58;
      let res = await this.$contract.userData(address);
      let res2 = await this.$contract.calculateStaticIncome(address);
      this.sta = this.$util.hexTo10To6number3(res2._hex);
      this.dynamic = this.$util.hexTo10To6number3(res.dynamicIncome._hex);
      this.money = this.$util.hexTo10To6number3(res.poolReward._hex);
      this.stats = this.$util.hexTo10To6number3(res.income._hex);
      console.log('提现查询的数据', res, res2);
    },

    async onSubmit(values) {
      console.log('点击了提现');
      let res = await this.$contract.take();
      // 重新获取数据
      await this.getData();
      console.log('提现的返回值', res);
    },


  }
};
</script>

