  //用户注册
  async signup(referrer) {
    try {
      let result = await this.gMasterContract.signup(referrer).send({
        feeLimit: 100000000,
        callValue: 0,
        // true就是20块确认才会给返回值, false就是1个确认就给返回值;
        shouldPollResponse: false
      });
      console.log('用户注册的返回值', result);
      return result;
    } catch (e) {
      console.log("signup error:",e);
    }
  },

  // 投资USDT参与master game  ====>对应的是调用主合约的join接口参与
