  //获取用户数据
  async userData(_addr) {
    let res = await this.gMasterContract.accounts(_addr).call();
    let joinTime = res.joinTime._hex;
    let dynamicIncome = res.dynamicIncome._hex;
    let poolReward = res.poolReward._hex;//====>对应主网页 "奖池奖金"
    let deposit = res.deposit._hex;//====>对应主网页 "存入资金"
    let maxIncome = res.maxIncome._hex;
    let isConsul = res.isConsul;
    let takedIncome = res.income._hex;
    let untakeIncome = dynamicIncome + poolReward;
    return res;
  },

