/* 自定义工具函数 */
// 导入数字精度计算
const Decimal = require('decimal');

const util = {
	//  地址缩减
	addressCurtail(address) {
		// 0-3(3个), 倒数4(4个);
		let a1 = address.slice(0, 3);
		let a2 = address.slice(-4);
		return a1 + '...' + a2;
	},

	// 如果是T9y...uWwb代表410..地址, 替换成000...0000
	addressReplace(address) {
		if(address === 'T9y...uWwb') {
			return '000...0000';
		}else {
			return address;
		}
	},

	// 常用16进制->10进制->除6位->保留小数点3位
	hexTo10To6number3(number) {
		/* let a = 100000000000000000000;
		let b = 2000000000000000000000000000.999;
		let c = new Decimal(a).add(new Decimal(b)).toNumber();
		console.log('cc--', c)
		c = c.toLocaleString();
		console.log('cc==', c);
		c = c.replace(/[,]/g, '');
		console.log('cccccccc',c); */
		return this.numberFormat(this.div10e6(this.hexToDec(number)));
	},

	// 常用16进制->10进制->除18位->保留小数点3位
	hexTo10To18number3(number) {
		return this.numberFormat(this.div10e18(this.hexToDec(number)));
	},

	// 金额保留小数点后3为位
	numberFormat(number) {
		// 这个数字就是可以保留位数, 可以随时更改;
    number = number * 1;
		const control = 3;
		if (Number.isInteger(number)) {
			return number;
		} else {
			number = number.toFixed(control);
			return number;
		}
	},
	numberFormat2(number) {
		// 这个数字就是可以保留位数, 可以随时更改;
		number = number * 1;
		const control = 3;
		if (Number.isInteger(number)) {
			return number;
		} else {
			number = number.toFixed(control);
			return number;
		}
	},

	// usdt精度 10**6;
	mul10e6(value) {
		return value * 1 * 10 ** 6;
	},
	// ticket精度 10**18;
	mul10e18(value) {
		value = new Number(value * 1 * (10 ** 18));
		value = value.toLocaleString();
		let arr = value.split(',');
		let v = '';
		for (var i = 0; i < arr.length; i++) {
			v += arr[i]
		}
		return v;
	},
	div10e6(value) {
		value = value * 1;
		if (value === 0) {
			return 0;
		}
		return value / 1000000;
	},
	div10e18(value) {
		value = value * 1;
		if (value === 0) {
			return 0;
		}
		return value / 1000000000000000000;
	},

	// 16进制转10进制
	hexToDec(number) {
		return parseInt(number, 16);
	},

	// 格式化日倒计时
	dayFormat(time) {
		let date;
		if (time <= 0 || time > 86400) {
			date = {
				h: '00',
				m: '00',
				s: '00',
			}
		} else {
			date = {
				h: Math.floor(time / (60 * 60) % 24) < 10 ? '0' + Math.floor(time / (60 * 60) % 24) : Math.floor(time / (60 * 60) %
					24),
				m: Math.floor((time / 60) % 60) < 10 ? '0' + Math.floor((time / 60) % 60) : Math.floor((time / 60) % 60),
				s: Math.floor(time % 60) < 10 ? '0' + Math.floor(time % 60) : Math.floor(time % 60)
			}
		}
		return date;
	},
	// 格式化周倒计时
	weekFormat(time) {
		let date;
		if (time <= 0 || time > 2592000) {
			date = {
				d: '00',
				h: '00',
				m: '00',
				s: '00',
			}
		} else {
			date = {
				d: Math.floor(time / (60 * 60 * 24) % 30) < 10 ? '0' + Math.floor(time / (60 * 60 * 24) % 30) : Math.floor(time /
					(60 * 60 * 24) % 30),
				h: Math.floor(time / (60 * 60) % 24) < 10 ? '0' + Math.floor(time / (60 * 60) % 24) : Math.floor(time / (60 * 60) %
					24),
				m: Math.floor((time / 60) % 60) < 10 ? '0' + Math.floor((time / 60) % 60) : Math.floor((time / 60) % 60),
				s: Math.floor(time % 60) < 10 ? '0' + Math.floor(time % 60) : Math.floor(time % 60)
			}
		}
		return date;
	},
	// 无止境的日倒计时, 很多很多天
	daysFormat(time) {
		let date;
		if (time <= 0) {
			date = {
				d: '00',
				h: '00',
				m: '00',
				s: '00',
			}
		} else {
			date = {
				d: Math.floor(time / (60 * 60 * 24) % 30) < 10 ? '0' + Math.floor(time / (60 * 60 * 24) % 30) : Math.floor(time /
					(60 * 60 * 24) % 30),
				h: Math.floor(time / (60 * 60) % 24) < 10 ? '0' + Math.floor(time / (60 * 60) % 24) : Math.floor(time / (60 * 60) %
					24),
				m: Math.floor((time / 60) % 60) < 10 ? '0' + Math.floor((time / 60) % 60) : Math.floor((time / 60) % 60),
				s: Math.floor(time % 60) < 10 ? '0' + Math.floor(time % 60) : Math.floor(time % 60)
			}
		}
		return date;
	},

	// 时间格式天:时:分:秒
	dayTime(time) {
		if (time <= 0) {
			return '00:00:00:00'
		}
		let d = Math.floor(time / (60 * 60 * 24) % 30) < 10 ? '0' + Math.floor(time / (60 * 60 * 24) % 30) : Math.floor(time /
			(60 * 60 * 24) % 30);
		let h = Math.floor(time / (60 * 60) % 24) < 10 ? '0' + Math.floor(time / (60 * 60) % 24) : Math.floor(time / (60 *
			60) % 24);
		let m = Math.floor((time / 60) % 60) < 10 ? '0' + Math.floor((time / 60) % 60) : Math.floor((time / 60) % 60);
		let s = Math.floor(time % 60) < 10 ? '0' + Math.floor(time % 60) : Math.floor(time % 60);
		return d + ':' + h + ':' + m + ':' + s;
	},
	dayTime2(time) {
		if (time <= 0) {
			return '00:00:00:00'
		}
		let d = Math.floor(time / (60 * 60 * 24)) < 10 ? '0' + Math.floor(time / (60 * 60 * 24)) : Math.floor(time /
			(60 * 60 * 24));
		let h = Math.floor(time / (60 * 60) % 24) < 10 ? '0' + Math.floor(time / (60 * 60) % 24) : Math.floor(time / (60 *
			60) % 24);
		let m = Math.floor((time / 60) % 60) < 10 ? '0' + Math.floor((time / 60) % 60) : Math.floor((time / 60) % 60);
		let s = Math.floor(time % 60) < 10 ? '0' + Math.floor(time % 60) : Math.floor(time % 60);
		return d + ':' + h + ':' + m + ':' + s;
	},
	// 时间格式时:分:秒
	hoursTime(time) {
		if (time <= 0) {
			return '00:00:00'
		}
		let h = Math.floor(time / (60 * 60) % 24) < 10 ? '0' + Math.floor(time / (60 * 60) % 24) : Math.floor(time / (60 *
			60) % 24);
		let m = Math.floor((time / 60) % 60) < 10 ? '0' + Math.floor((time / 60) % 60) : Math.floor((time / 60) % 60);
		let s = Math.floor(time % 60) < 10 ? '0' + Math.floor(time % 60) : Math.floor(time % 60);
		return h + ':' + m + ':' + s;
	}

}

export default util;
