<template>
  <div class="popup">
    <van-popup
      v-model="show"
      position="left"
      ref="myPopup"
      :style="{ width: '80%', height: '100%' }"
    >
      <div class="box">
        <van-switch
          :value="checked"
          v-model="checked"
          active-color="#f9f3ef"
          @click="doSwitch"
        />
        <div class="txt">
          <div class="language" v-for="(list, index) in language" :key="index">
            <span
              @click="change(index)"
              :class="{ active: currentIndex === index }"
              :value="checked"
              >{{ list }}</span
            >
          </div>
        </div>
      </div>

      <h3>LOGO</h3>
      <p class="code">
        {{ $t("popup.InvitationCode") }}:{{ codeValue
        }}<i
          class="iconfont copy"
          v-clipboard:copy="codeValue"
          v-clipboard:success="type => onCopyResult('success')"
          v-clipboard:error="type => onCopyResult('error')"
          >&#xe61b;</i
        >
      </p>
      <div class="nav">
        <div class="list" @click="toIndex">
          <img src="@/assets/menu_item_icon_1.png" alt="" />
          <span>{{ $t("popup.MainPage") }}</span>
        </div>
        <div class="list" @click="toInvest">
          <img src="@/assets/menu_item_icon_2.png" alt="" />
          <span>{{ $t("popup.Contract") }}</span>
        </div>
        <div class="list" @click="toRank">
          <img src="@/assets/menu_item_icon_3.png" alt="" />
          <span>{{ $t("popup.TournamentPrizePo") }}</span>
        </div>
        <div class="list" @click="toExplain">
          <img src="@/assets/menu_item_icon_4.png" alt="" />
          <span>{{ $t("popup.GameShows") }}</span>
        </div>
        <div class="list" @click="toInvite">
          <img src="@/assets/menu_item_icon_5.png" alt="" />
          <span>{{ $t("popup.InviteFriends") }}</span>
        </div>
        <div class="list" @click="showDialog = true">
          <img src="@/assets/memu_item_icon_6.png" alt="" />
          <span>{{ $t("popup.BindingInvitationCo") }}</span>
        </div>
      </div>
      <div class="withdrawal">
        <span class="left"
          ><img src="@/assets/wallet.png" alt="" />{{ wallet }} USDT</span
        >
        <span class="right" @click="toWithdrawal"
          >{{ $t("popup.ToWithdraw")
          }}<span class="iconfont you">&#xe79a;</span></span
        >
      </div>
      <!--<div class="popupBtm">
        <p>{{ $t("popup.CumulativeDividendinc") }}：{{ share }} USDT</p>
        <p>{{ $t("popup.AccumulatedTeamRevenu") }}：{{ team }} USDT</p>
        <p>
          {{ $t("popup.TotalTeamSize") }}：{{ people }} {{ $t("popup.people") }}
        </p>
        <div class="buttom">
