      </div>-->
    </van-popup>
    <van-dialog
      v-model="showDialog"
      :title="this.$t('popup.BindingInvitationCo')"
      theme="round"
      show-cancel-button
      :cancelButtonText="this.$t('popup.Cancel')"
      :confirm-button-text="this.$t('popup.Binding')"
      confirm-button-color="#fff"
      @confirm="myConfirm"
    >
      <van-field
        :placeholder="this.$t('popup.BindingInput')"
        v-model="address"
      ></van-field>
    </van-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 判断是否已经注册了
      id: 1,
      show: false,
      showDialog: false,
      address: "",
      checked: true,
      currentIndex: 1,
      language: ["中文", "EN"],
      codeValue: "df1374",
      // usdt余额
      wallet: "0",
      share: "100.23",
      team: "100.23",
      people: "100"
    };
  },
  async mounted() {
    let l = localStorage.getItem("lang");
    if (l == 1) {
      this.checked = true;
      this.currentIndex = 1;
      this.$i18n.locale = "en";
    } else {
      this.checked = false;
      this.currentIndex = 0;
      this.$i18n.locale = "zh";
    }
    // 同步锁, 等$isResolve执行完毕了才会执行这个
    await this.$onLaunched;
    // 获取数据
    await this.getUsdt();
    await this.getData();
    // console.log(localStorage.getItem("lang"), this.checked, this.currentIndex, this.$i18n.locale);
  },
  methods: {
    // 获取usdt余额
    async getUsdt() {
      let address = await this.$contract.defaultAddress();
      address = address.base58;
      let usdtBalance = await this.$contract.getUsdtBalance(address);
      this.wallet = this.$util.hexTo10To6number3(usdtBalance);
    },

    // 获取用户的信息
    async getData() {
      let address = await this.$contract.defaultAddress();
      address = address.base58;
      let res = await this.$contract.userData(address);
      let id = this.$util.hexToDec(res.id._hex);
      if(id === 0) {
        console.log('没有注册');
        this.id = 0;
      }else {
        this.id = 1;
        console.log('注册了');
      }
    },

    // 点击了注册
    async myConfirm() {
      if(this.id === 0) {
      }else {
        alert(this.$t("popup.isRegister"));
        return
      }

      // 同步锁, 等$isResolve执行完毕了才会执行这个
      await this.$onLaunched;
      let address;
      if(this.address === '') {
        address = this.$contract.defaultReferrer;
      }else  {
        address = this.address;
      }
      console.log('注册的推荐人地址', address);
      let res = await this.$contract.signup(address);
      console.log('注册的返回值', res);
    },

    toWithdrawal() {
      this.$router.push({ path: "/withdrawal" });
    },
    change(index) {
      if (this.currentIndex === 1) {
        this.checked = false;
        this.$i18n.locale = "zh";
        localStorage.setItem("lang", 0);
      } else if (this.currentIndex === 0) {
        this.checked = true;
        this.$i18n.locale = "en";
        localStorage.setItem("lang", 1);
      }
      this.currentIndex = index;
      console.log("====11", localStorage.getItem("lang"), this.checked, this.currentIndex, this.$i18n.locale);
    },
    doSwitch() {
      if (this.checked === false) {
        this.currentIndex = 1;
        this.$i18n.locale = "zh";
        localStorage.setItem("lang", 0);
      } else {
        this.currentIndex = 0;
        this.$i18n.locale = "en";
        localStorage.setItem("lang", 1);
      }
      console.log("====22", localStorage.getItem("lang"));
    },
    onCopyResult(result) {
      this.$toast.success(this.$t("popup.copy"));
    },
    toIndex() {
      this.$router.push({ name: "index" });
    },
    toInvest() {
      this.$router.push({ name: "invest" });
    },
    toRank() {
      this.$router.push({ name: "rank" });
    },
    toExplain() {
      this.$router.push({ name: "explain" });
    },
    toInvite() {
      this.$router.push({ name: "invite" });
    }
  }
};
</script>

