import Vue from "vue";
import VueRouter from "vue-router";
import index from "@/views/index/index.vue";
import invite from "@/views/invite/invite.vue";
import invest from "@/views/invest/invest.vue";
import contract from "@/views/contract/contract.vue";
import withdrawal from "@/views/withdrawal/withdrawal.vue";
import rank from "@/views/rank/rank.vue";
import explain from "@/views/explain/explain.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/index",
    name: "index",
    alias: "/",
    component: index
  },
  {
    path: "/invite",
    name: "invite",
    component: invite
  },
  {
    path: "/invest",
    name: "invest",
    component: invest
  },
  {
    path: "/contract",
    name: "contract",
    component: contract
  },
  {
    path: "/withdrawal",
    name: "withdrawal",
    component: withdrawal
  },
  {
    path: "/rank",
    name: "rank",
    component: rank
  },
  {
    path: "/explain",
    name: "explain",
    component: explain
  }
];

const router = new VueRouter({
  routes
});

export default router;
