<template>
  <div class="rank">
    <popupNavBar />
    <img src="../../assets/rank_bg.png" alt="" class="headerImg" />
    <header>
      <div class="earnings">
        <p class="earning">{{ $t("rank.CommissionBonusPool") }}</p>
        <div class="num">{{pool}}</div>
      </div>
      <div class="box">
        <h1>{{ $t("rank.gameRule") }}</h1>
        <p>
          {{ $t("rank.gameIntroduction") }}
        </p>
      </div>
    </header>
    <footer>
      <h2>{{ $t("rank.DirectRanking") }}</h2>
      <table>
        <tr>
          <div class="title">
            <th>{{ $t("rank.ranking") }}</th>
            <th>{{ $t("rank.accounts") }}</th>
            <th>{{ $t("rank.performance") }}</th>
            <th>{{ $t("rank.bonus") }}</th>
          </div>
        </tr>
        <tr v-for="(item, index) in list" :key="index">
          <div class="row">
            <td v-if="index === 0"><img src="@/assets/rank_icon_1.png" alt="" /></td>
            <td v-else-if="index === 1"><img src="@/assets/rank_icon_2.png" alt="" /></td>
            <td v-else-if="index === 2"><img src="@/assets/rank_icon_3.png" alt="" /></td>
            <td v-else class="weight">{{index+1}}</td>
            <td>{{item.a}}</td>
            <td>{{item.b}}</td>
            <td>{{item.c}}</td>
          </div>
        </tr>
      </table>
    </footer>
  </div>
</template>

<script>
import popupNavBar from "@/components/popupNavBar";
export default {
  components: { popupNavBar },
  data() {
    return {
      // 手续费奖池
      pool: '0',
      // 排行榜列表
      list: [],
    }
  },

  async created() {
    // 同步锁, 等$isResolve执行完毕了才会执行这个
    await this.$onLaunched;
    await this.getPool();
    await this.getList();
  },

  methods: {
    // 查询手续费池子
    async getPool() {
      let res = await this.$contract.takeFeePool();
      res = this.$util.hexTo10To6number3(res.amount._hex);
      this.pool = res;
      console.log('查询手续费池子', res);
    },

    // 查询排行榜
    async getList() {
      console.log('开始查询排行榜');
      this.list = [];
      for (let i = 0; i < 10; i++) {
        let res = await this.$contract.perfRankTop20(i);
        let a = res.userAddr;
        a = await this.$contract.addressFromHex(a);
        a = this.$util.addressCurtail(a);
        let b = res.performance._hex;
        b = this.$util.hexTo10To6number3(b);
        let c = res.reward._hex;
        c = this.$util.hexTo10To6number3(c);
        console.log(res);
        this.list.push({
          a,b,c
        })
      }
    },

  }

};
</script>

