import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import Vant from "vant";
import "vant/lib/index.css";
import "../src/style/comm.css";
import "amfe-flexible";
import "../src/style/iconfont/iconfont.css";
import VueClipboard from "vue-clipboard2";
import contract02 from "./common/contract02";
import util from './common/util.js';
import VueI18n from "vue-i18n";

Vue.use(Vant);
Vue.use(VueClipboard);
Vue.use(VueI18n); // 通过插件的形式挂载，通过全局方法 Vue.use() 使用插件const i18n = new VueI18n({
// 同步锁 -- 这个项目是波场的USDT-Game项目
Vue.prototype.$onLaunched = new Promise(resolve => {
  Vue.prototype.$isResolve = resolve
});
const i18n = new VueI18n({
  locale: "en", // 语言标识 //this.$i18n.locale // 通过切换locale的值来实现语言切换
  messages: {
    zh: require("./components/language/zh"), //引入language-zh.js  language-en,js
    en: require("./components/language/en")
  }
});
Vue.config.productionTip = false;

// 全局的事件总线对象
Vue.prototype.$bus = new Vue();
