<template>
  <div class="invest">
    <popupNavBar/>
    <header>
      <div class="line"></div>
      <div class="percent">
        <p class="subTitle">{{ $t("invest.MonthlyReturns") }}</p>
        <p class="p1">{{ staticRate }}</p>
      </div>
    </header>
    <div class="toInvest">
      <h1>{{ $t("invest.TheGameIsToInvest") }}</h1>
      <h2>{{ $t("invest.enter") }}</h2>
      <van-stepper
        v-model="join"
        @keyup.native="onChange"
        min="100"
        max="10000"
      />
      <h3>
        {{ $t("invest.balance") }} <span class="color">{{this.balance}}</span> USDT
      </h3>
      <div class="investBtn">
        <van-button v-if="this.isApp" @click="toDialog = true">{{
          $t("invest.invest")
          }}
        </van-button>
        <van-button v-else @click="sendApprove">{{ $t("invest.approve") }}
        </van-button>
      </div>
    </div>
    <div class="rule">
      <h1>{{ $t("invest.gameRule") }}</h1>
      <p class="introduce">
        {{ $t("invest.gameIntroduction") }}
      </p>
    </div>
    <footer>
      <div class="subTitle">
        <div class="line"></div>
        <span class="s">{{ $t("invest.GameHistory") }}</span>
      </div>
      <table>
        <tr>
          <th>{{ $t("invest.player") }}</th>
          <th>{{ $t("invest.bet") }}</th>
          <th>{{ $t("invest.earnings") }}</th>
        </tr>
        <tr v-for="(item, index) in list" :key="index">
          <td>{{item.a}}</td>
          <td>{{item.b}}</td>
          <td>{{item.c}}</td>
        </tr>


      </table>
    </footer>
    <van-dialog
      v-model="toDialog"
      :title="this.$t('invest.title')"
      theme="round"
      show-cancel-button
      :cancelButtonText="this.$t('invest.cancel')"
      :confirm-button-text="this.$t('invest.buttonText')"
      confirm-button-color="#fff"
      @cancel="myCancel()"
      @confirm="myConfirm()"
    >{{ $t("invest.invest") }}：{{ join }}.00USDT
    </van-dialog>
  </div>
</template>

<script>
  import popupNavBar from "@/components/popupNavBar";

  export default {
    data() {
      return {
        // 月收益率; (死数据)
        staticRate: "36.00%",
        // 显示大于1000usdt的投资数量
        join: 1000,
        // 输入框得最小值和最大值
        mixValue: 1,
        maxValue: 999999999,
        // usdt余额
        balance: 0,
        // 投资记录
        invests: 0,
        // 弹框的显示
        toDialog: false,
        // 是否授权
        isApp: false,
        // 最新记录的数组
        list: [],
      };
    },

    components: {
      popupNavBar
    },

    async created() {
      // 同步锁, 等$isResolve执行完毕了才会执行这个
      await this.$onLaunched;
      await this.getUSDTBalance();
      await this.getAppr();
      await this.getIds();
    },

    methods: {
      toContract() {
        this.$router.push("/contract");
      },
      toWithdrawal() {
        this.$router.push({path: "/withdrawal"});
      },
      onChange() {
        if (this.join > 10000) {
          this.join = 10000;
        } else if (this.join < 100) {
          this.join = 100;
        }
      },

      // 获取usdt余额
      async getUSDTBalance() {
        let address = await this.$contract.defaultAddress();
        address = address.base58;
        let usdtBalance = await this.$contract.getUsdtBalance(address);
        this.balance = this.$util.hexTo10To6number3(usdtBalance);
      },

      // 点击了取消
      async myCancel() {
      },

      // 查询授权sudt给合约得余额
      async getAppr() {
        let address = await this.$contract.defaultAddress();
        let from = address.base58;
        let to = this.$contract.masterAddress;
        let res = await this.$contract.getAppr(from, to);
        let value = this.$util.hexToDec(res._hex);
        if(value < 1) {
          this.isApp = false;
        }else {
          this.isApp = true;
        }
        console.log('查询授权', value, this.isApp);
      },

      // 授权usdt给合约
      async sendApprove() {
        console.log('点击了授权');
        // 同步锁, 等$isResolve执行完毕了才会执行这个
        await this.$onLaunched;
        let to = this.$contract.masterAddress;
        let value = '99999999000000';
        let res = await this.$contract.sendApprove(to, value);
        console.log('授权usdt的返回值', res);
        // 从新获取数据
        await this.getAppr();
      },

      // 点击了确认投资
      async myConfirm() {
        let value = this.join;
        value = this.$util.mul10e6(value);
        console.log('投资的参数', value);
        let res = await this.$contract.join(value);
        // 从新获取数据
        await this.getUSDTBalance();
        console.log('投资的返回值', res);
      },

      // 获取用户的人数
      async getIds() {
        // 获取总的id长度
        let res = await this.$contract.userId();
        let id = this.$util.hexToDec(res._hex);
        // 开始循环的起步值
        let i = 0;
        if(id > 3) {
          i = id - 3;
        }
        console.log('开始查询记录', i, id);
        this.list = [];
        for (i; i < id; i++) {
          // 查询id对应的地址, hex类型
          let res1 = await this.$contract.getAddressByID(i);
          res1 = await this.$contract.addressFromHex(res1);
          // 获取投注信息
          let res2 = await this.$contract.userData(res1);
          res2 = this.$util.hexTo10To6number3(res2.deposit._hex);
          // 获取收益
          let res3 = await this.$contract.calculateStaticIncome(res1);
          res3 = this.$util.hexTo10To6number3(res3._hex);
          this.list.push({
            a: this.$util.addressCurtail(res1),
            b: res2,
            c: res3
          })
        }
        /*let res2 = await this.$contract.getAddressByID(5);
        res2 = await this.$contract.addressFromHex(res2);
        let res3 = await this.$contract.userData(res2);*/
        //console.log('res:', id, res2, res3);
       // let res2 = await this.$contract.calculateStaticIncome(address);
      },

    }


  };
</script>

